.sk-cube-grid {
    width: 40px;
    height: 40px;
    margin: 0;
  }
  
  .sk-cube-grid .sk-cube {
    width: 33%;
    height: 33%;
    background-color: #333;
    float: left;
    -webkit-animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out;
            animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out; 
  }
  .sk-cube-grid .sk-cube1 {
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s; }
  .sk-cube-grid .sk-cube2 {
    -webkit-animation-delay: 0.3s;
            animation-delay: 0.3s; }
  .sk-cube-grid .sk-cube3 {
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s; }
  .sk-cube-grid .sk-cube4 {
    -webkit-animation-delay: 0.1s;
            animation-delay: 0.1s; }
  .sk-cube-grid .sk-cube5 {
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s; }
  .sk-cube-grid .sk-cube6 {
    -webkit-animation-delay: 0.3s;
            animation-delay: 0.3s; }
  .sk-cube-grid .sk-cube7 {
    -webkit-animation-delay: 0s;
            animation-delay: 0s; }
  .sk-cube-grid .sk-cube8 {
    -webkit-animation-delay: 0.1s;
            animation-delay: 0.1s; }
  .sk-cube-grid .sk-cube9 {
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s; }
  
  @-webkit-keyframes sk-cubeGridScaleDelay {
    0%, 70%, 100% {
      -webkit-transform: scale3D(1, 1, 1);
              transform: scale3D(1, 1, 1);
    } 35% {
      -webkit-transform: scale3D(0, 0, 1);
              transform: scale3D(0, 0, 1); 
    }
  }
  
  @keyframes sk-cubeGridScaleDelay {
    0%, 70%, 100% {
      -webkit-transform: scale3D(1, 1, 1);
              transform: scale3D(1, 1, 1);
    } 35% {
      -webkit-transform: scale3D(0, 0, 1);
              transform: scale3D(0, 0, 1);
    } 
  }
#Poster {
    width: 100%;
    text-align: center;
    position: relative;
}

#Poster img {
    max-width: 100%;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
    background-color: rgb(204, 204, 204);
}

#posterImage {
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
}

#spinner {
    position: absolute;
    left: calc(50% - 20px);
    top: calc(50% - 20px);
}
#castItemsContainer {
    display: block;
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
    max-width: 100%;
    white-space: nowrap;
    overflow-x: hidden;
    
    box-sizing: border-box;

    border-radius: 10px;
    border: 15px transparent solid;
    background-color: rgba(214, 214, 214, 0.5);
    box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.5);

    z-index: 2;    
}


@media only screen and (max-width: 1100px) {
    #castItemsContainer {
        overflow-x: scroll;
        border-bottom: 0;
    }
}

.castItem {
    display: inline-block;
    width: 12vmax;
    margin-left: 1vmax;
    margin-right: 1vmax;
}

.castItem.last {
    margin-right:0
}
.castItem.first {
    margin-left:0
}

.castItem img {
    max-width: 100%;
    border-radius: 4px;
}


.castText {
    max-width: 100%;
    text-align: center;
    font-size: 1.1vmax;
    box-sizing: border-box;
}

.castText div {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
#plotTextContainer {
    box-sizing: border-box;

    border-radius: 10px;
    border: 15px transparent solid;
    background-color: rgba(214, 214, 214, 0.5);
    box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.5);

    z-index: 3;
}
#GameContainer {

    box-sizing: border-box;

    padding-top: 70px;

    padding-bottom: 60px;

    display: -webkit-flex;

    display: flex;
    width: 100%;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

#posterContainer {
    width: 90%;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    margin-bottom: 25px;
}

#castContainer {
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
    max-width: 90%;
}

#plotContainer {
    width: 700px;
    max-width: 90%;
}

.hintContainer {
    margin-bottom: 25px;
    
}

.hintContainer.hide {
    height: 0;
    margin: 0;
    -webkit-transform-origin: top center;
            transform-origin: top center;
    -webkit-transform: scaleX(0) scaleY(0);
            transform: scaleX(0) scaleY(0);
    
}
.hintContainer.show {
    height: auto;
    -webkit-transform-origin: top center;
            transform-origin: top center;
    -webkit-transform: scale(1) skew(100,0);
            transform: scale(1) skew(100,0);
    transition: -webkit-transform 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
    transition: transform 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
    transition: transform 0.5s cubic-bezier(0.215, 0.61, 0.355, 1), -webkit-transform 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
    transition-delay: 0.25s;
}

.hintButton {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;

    width: 300px;
    max-width: 60%;
    height: 40px;
    background-color: rgba(190, 190, 190, 0.5);
    border: 1px rgba(201, 201, 201, 0.3) solid;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
    border-radius: 20px;

}

.hintButton.hide {
    height:0;
    -webkit-transform: scale(0);
            transform: scale(0);
    transition: height 0.25s cubic-bezier(1, 1, 1, 0),
                -webkit-transform 0.2s ease-in;
    transition: transform 0.2s ease-in,
                height 0.25s cubic-bezier(1, 1, 1, 0);
    transition: transform 0.2s ease-in,
                height 0.25s cubic-bezier(1, 1, 1, 0),
                -webkit-transform 0.2s ease-in
}

.hintButton.show {
    -webkit-transform: scale(1);
            transform: scale(1);
}

.hintButton.hideLoad {
    transition: none !important;
}


.button {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    position: fixed;
    top: 50%;
    width: 8vmax;
    height: 8vmax;
    border-radius: 50%;
    background-color: white;
    

    z-index: 10;
}

.button span {
    -webkit-transform: translateY(-0.2vmax);
            transform: translateY(-0.2vmax)
}

.button.right {
    background-color: rgb(0, 255, 0, 0.5);
    right: 5vw;
}

.button.left {
    background-color: rgba(255, 0, 0, 0.5);
    left: 5vw;
}
